<template><!---->
    <div>
        <a-layout-header class="header">
            <div class="logo"></div>
            <a-menu
                theme="dark"
                mode="horizontal"
                :defaultSelectedKeys="items"
                :style="{ lineHeight: '64px' }"
            >
                <a-menu-item key="1" @click="goTopublic"></a-menu-item>
                <a-menu-item key="2" @click="goTosystem"></a-menu-item>
                <!-- <a-menu-item ></a-menu-item> -->
                 <a-sub-menu  key="3" @click="goToservicem">
                    <span slot="title" class="submenu-title-wrapper"></span>
                    <a-menu-item-group>
                        <a-menu-item key="setting:1"></a-menu-item>
                        <a-menu-item key="setting:2"></a-menu-item>
                        <a-menu-item key="setting:3"></a-menu-item>
                    </a-menu-item-group>
                </a-sub-menu>
                <a-menu-item key="4" @click="goTotradelinks"></a-menu-item>
                <a-menu-item key="5" @click="goTostatistic"></a-menu-item>
                <a-menu-item key="6" @click="goTo"></a-menu-item>
                <a-menu-item key="7" @click="goTomywork"></a-menu-item>
            </a-menu>
        </a-layout-header>
    </div>
</template>

<script>

export default {
    props: ['items'],//
    data(){
        return{

        }
    },
    created(){
       console.log(this.selectedKey)
    },
    methods:{
        goTopublic(){//
            this.$router.push({ path: "/home" });
            // this.selectedKey = ["1"];
        },
        goTosystem(){//
            this.$router.push({ path: "/system" });
            this.selectedKey = ["2"];
        },
        goToservicem(){//
            this.$router.push({ path: "/servicemanage" });
            this.selectedKey = ["3"];
        },
        goTotradelinks(){//
            this.$router.push({ path: "/transactionLink" });
            this.selectedKey = ["4"];
        },
        goTostatistic(){//
            this.$router.push({ path: "/Statistics" });
            this.selectedKey = ["5"];
        },
        goTo(){//
            this.$router.push({ path: "/platform" });
            this.selectedKey = ["6"];
        },
        goTomywork(){//
            this.$router.push({ path: "/myworkspace" });
            this.selectedKey = ["7"];
        }
    }

}
</script>
<style scoped>
.ant-tabs-tabpane{
  height: 800px;
  overflow-y: scroll;
}
#components-layout-demo-top-side-2 .logo {
  width: 172px;
    font-size: 24px;
  height: 31px;
  line-height: 31px;
  /* background: rgba(255,255,255,.2); */
  margin: 16px 28px 16px 0;
  color: #fff;
  font-weight: bold;
  float: left;

}
</style>



