import axios from "axios"; // 
import config from "./config.js"; // 
import Qs from "qs"; // 
// import store from "../store";
import router from "../router";


export default function $axios (options) {
  return new Promise((resolve, reject) => {
    const instance = axios.create({
      baseURL: config.baseURL,
      headers: {},
      transformResponse: [function(data) {}]
    });

    // request 
    instance.interceptors.request.use(
      config => {
        // Tip: 1
        //  vuex  loading 
        // const loading = this.$loading({
        //   lock: true,
        //   text: 'Loading',
        // });

        // Tip: 2
        //  token ,  vuex  localStorage
        // if (store.state.token) {
        //   config.headers["token"] = store.state.token; // token--['X-Token']key 
        // }
        //else {
        //   router.push({
        //     path: '/login'
        //   })
        // }
        //   if (store.state.token) {  // tokenhttp headertoken
        //     config.headers.Authorization = `token ${store.state.token}`;
        // }

        // Tip: 3
        // 
        if (
          config.method.toLocaleLowerCase() === "post" ||
          config.method.toLocaleLowerCase() === "put" ||
          config.method.toLocaleLowerCase() === "delete"
        ) {
          console.log(config.data);
          // config.data = qs.stringify(config.data)
        }
        return config;
      },
      error => {
        // ()
        // Tip: 4
        // loadding
        console.log("request:", error);
        // loading.close();
        //  1.
        if (
          error.code === "ECONNABORTED" &&
          error.message.indexOf("timeout") !== -1
        ) {
          console.log(
            "timeout/ "
          );
          // return service.request(originalRequest);//
        }
        //  2.
        const errorInfo = error.response;
        console.log(errorInfo);
        if (errorInfo) {
          // error =errorInfo.data//catch,Promise.reject
          const errorStatus = errorInfo.status; // 404 403 500 ... 
          router.push({
            path: `/error/${errorStatus}`
          });
        }
        return Promise.reject(error); // (catch)
      }
    );

    // response 
    instance.interceptors.response.use(
      response => {
        let data;
        // IE9response.dataundefinedresponse.request.responseText(Stringify)
        if (response.data == undefined) {
          data = response.request.responseText;
        } else {
          data = response.data;
        }
        // code
        switch (data.code) {
          case "1":
            Message.error(data.msg);
            break;
          case "2":
            // console.log(data.msg)
            // Message.error("");
            break;
          default:
        }
        // code
        // const err = new Error(data.description)

        // err.data = data
        // err.response = response

        // throw err
        return data;
      },
      err => {
        if (err && err.response) {
          switch (err.response.status) {
            case 400:
              err.message = "";
              break;

            case 401:
              err.message = "";
              break;

            case 403:
              err.message = "";
              break;

            case 404:
              err.message = `: ${err.response.config.url}`;
              break;

            case 408:
              err.message = "";
              break;

            case 500:
              err.message = "";
              break;

            case 501:
              err.message = "";
              break;

            case 502:
              err.message = "";
              break;

            case 503:
              err.message = "";
              break;

            case 504:
              err.message = "";
              break;

            case 505:
              err.message = "HTTP";
              break;

            default:
          }
        }
        console.error(err);
        //  element UI 
        // Message.error(`ERROR: ${err}`);
        return Promise.reject(err); // 
      }
    );

    // 
    instance(options)
      .then(res => {
        resolve(JSON.parse(res));
        return false;
      })
      .catch(error => {
        reject(error);
      });
  });
}



